<template>
	<div class="privacy-container">
		<div class="privacy-header">
			<div class="privacy-zhong">
				<div class="privacy-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="privacy-ti">隐私政策</div>
				<div></div>
			</div>
		</div>
		<div class="privacy-content">
			<div v-html="data.policy_content">
				<!--{{data.policy_content}}-->
			</div>
			<!--<div>
				腾讯严格遵守法律法规，遵循以下隐私保护原则，为您提供更加安全、可靠的服务：
			</div>
			<div>1、安全可靠：我们竭尽全力通过合理有效的信息安全技术及管理流程，防止您的信息泄露、损毁、丢失。</div>
			<div>2、自主选择：我们为您提供便利的信息管理选项，以便您做出合适的选择，管理您的个人信息。</div>
			<div>3、保护通信秘密：我们严格遵照法律法规，保护您的通信秘密，为您提供安全的通信服务。</div>
			<div>4、合理必要：为了向您和其他用户提供更好的服务，我们仅收集必要的信息。</div>
			<div>5、清晰透明：我们努力使用简明易懂的表述，向您介绍隐私政策，以便您清晰地了解我们的信息处理方式</div>
			<div>6、将隐私保护融入产品设计：我们在产品或服务开发的各个环节，综合法律、产品、设计等多方因素，融入隐私保护的理念。</div>
			<div class="privacy-lianxi">如您有问题，请联系我们。</div>-->
		</div>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: {}
			}
		},
		methods: {
			back(){
				window.history.back();
			},
			info_policy() {
				var that = this;
				that.$http.post('/help/info_policy', {}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.info_policy();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.privacy-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.privacy-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.privacy-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.privacy-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.privacy-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.privacy-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
		font-size: 1.4rem;
		text-align: justify;
	}
	.privacy-lianxi{
		padding-top: 4rem;
		padding-bottom: 7rem;
	}
</style>


